/*
| Partials
*/
.box {
  background: #f00;
  width: 200px;
  height: 200px;
  margin-bottom: 1em;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 1em;
  flex: 1;
}
.box:nth-child(2) {
  flex: 2;
}
.box__container {
  display: flex;
  flex-direction: row-reverse;
}
.Media {
  display: flex;
  background: #fff;
  padding: 20px;
}
.Media__figure {
  margin-right: 1em;
}
.Media__body p:first-child {
  margin-top: 0;
}
.Testimonial {
  border: 16px solid #fff;
}
.Testimonial--is-featured {
  width: 100%;
}
.Testimonial__body {
  padding: 0 50px;
}
.Testimonial__name {
  color: #f00;
}
.Testimonial--is-featured .Testimonial__name {
  text-align: left;
}
.grid__column {
  flex: 1;
  text-align: center;
  border: 1px solid #808080;
}
.grid__row {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2em;
}
.aside {
  flex-basis: 150px;
}
.primary {
  flex-basis: 500px;
}
body {
  font-family: sans-serif;
}
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}
li {
  margin: 20px 0;
}
a {
  color: #fff;
  text-decoration: none;
  display: inline-block;
  padding: 50px;
}
a:hover {
  text-decoration: underline;
}
nav > ul {
  display: flex;
  background: #000;
  align-items: center;
  justify-content: space-around;
}
nav li {
  flex: 1;
}
@media screen and (max-width: 480px) {
  nav > ul {
    flex-direction: column;
  }
}
header {
  background: #f00;
}
header h1 {
  margin: 0;
}
aside {
  background: #ff0;
}
main {
  background: #00f;
}
footer {
  background: #ffa500;
}
.app-container {
  display: flex;
  flex-wrap: wrap;
}
header,
footer {
  flex-basis: 100%;
}
main {
  flex: 1;
}
aside {
  flex-basis: 150px;
}
@media screen and (max-width: 480px) {
  .app-container > * {
    flex-basis: 100%;
  }
}
.navigation-bar ul {
  background: #008000;
  display: flex;
}
.navigation-bar ul,
.navigation-bar li {
  list-style: none;
}
.navigation-bar li {
  flex: 1;
  text-align: center;
}
.navigation-bar li:hover {
  background: #00f;
}
